import React from 'react'
import { Container, Col, Row } from 'react-bootstrap'
import * as C from './styles'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheck } from '@fortawesome/free-solid-svg-icons'

const Plans = () => {
    return (
        <Container fluid>
            <C.Section>
                <Row>
                    <Col>
                    </Col>
                    <Col>
                        <C.Card>
                            <C.H3>Hiper<br></br>Gesto</C.H3>
                            <C.CardText>Para quem quer ter um maior controle e crescer</C.CardText>
                            <C.CardText>Com o Hiper Gesto voc consegue controlar suas vendas e estoque, cadastrar clientes, cadastrar produtos com imagem e controlar seu financeiro. Pode tambm emitir nota fiscal de produtos (NF-e) ilimitada e fazer a gesto de comisso de vendas.</C.CardText>
                            <C.Button>Experimente grtis</C.Button>
                            <C.ListWrapper>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>4 usurios ou mais</C.Span>
                                </li>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>Funciona online e offline</C.Span>
                                </li>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>Treinamento e suporte por parceiros regionais qualificados</C.Span>
                                </li>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>Mais de 60 relatrios para gesto do negcio</C.Span>
                                </li>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>Gerencie 1 ou mais lojas</C.Span>
                                </li>

                                <li>
                                    <C.Icon><FontAwesomeIcon icon={faCheck}></FontAwesomeIcon></C.Icon>
                                    <C.Span>Opcionais: boletos bancrios, app de vendas, nota fiscal de servios, conta digital, maquininha de carto e TEF</C.Span>
                                </li>

                            </C.ListWrapper>
                        </C.Card>
                    </Col>
                    <Col>
                        <C.Card>
                        </C.Card>
                    </Col>
                    <Col>
                        <C.Card>
                        </C.Card>
                    </Col>
                    <Col></Col>
                </Row>
            </C.Section>
        </Container>
    )
}

export default Plans